.full-projects{
    min-height: 100vh;
    background-color: rgb(4, 41, 58);
    color: whitesmoke;
    font-size: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.full-projects h1{
    padding-top: 50px;
}
.main-contact{
    min-height: 100vh;
    background-color: rgb(4, 41, 58);
    color: whitesmoke;
    font-size: 48px;
}
